import Vue from 'vue'
import VueRouter from 'vue-router'
import firebase from "firebase";
import Login from "../components/Login";
import Dashboard from "../components/Dashboard";

Vue.use(VueRouter)

const routes = [
	{
		path: '/',
		name: 'dashboard',
		component: Dashboard,
		meta: {
			requriesAuth: true
		}
	},
	{
		path: '/login',
		name: 'login',
		component: Login,
		meta: {
			requriesGuest: true
		}
	}
]

const router = new VueRouter({
	routes
})


router.beforeEach((to, from, next) => {
	// Check for requiredAuth
	if (to.matched.some(record => record.meta.requriesAuth)) {
		// Now check if logged in using firebase
		if (!firebase.auth().currentUser) {
			// Go to login
			next({
				path: '/login',
				query: {
					redirect: to.fullPath
				}
			});
		} else {
			// Proceed to route
			next();
		}
	} else if (to.matched.some(record => record.meta.requriesGuest)) {
		// Check if logged in
		if (firebase.auth().currentUser) {
			// Go to login
			next({
				path: '/',
				query: {
					redirect: to.fullPath
				}
			});
		} else {
			// Proceed to route
			next();
		}
	} else {
		next();
	}
});


export default router
