import Vue from 'vue'
import App from './App.vue'
import router from './router'
import firebase from "firebase";

Vue.config.productionTip = false

Vue.prototype.recaptcha = window.recaptchaVerifier
Vue.prototype.otpConfirmation = window.confirmationResult

let app;

firebase.auth().onAuthStateChanged(user => {
  if (!app) {
    app = new Vue({
      router,
      render: h => h(App)
    }).$mount('#app');
  }
});


