<script>
	import db from "./firebase_config/FirebaseInit";
	import firebase from 'firebase';

	export default {
		name: "dashboard",
		data() {
			return {
				users: {
					full_name: '',
					admission_no: '',
					phone_no: '',
					path_to_file: '',
					pdf_file_props: {
						size: '',
						no_of_copies: '',
						double_sided: false,
					},
					pending: true,
					cost: ''
				},
				imageData: null,
				picture: null,
				uploadValue: 0,

			}
		},
		created() {


			db.collection("users").get().then(querySnapshot => {
				querySnapshot.forEach(doc => {
					// console.log(doc.data())
					// const data = {
					// 	"id": doc.id,
					// 	"employee_id": doc.data().employee_id,
					// 	"name": doc.data().name,
					// 	"dept": doc.data().dept,
					// 	"position": doc.data().position
					// }
					// this.employees.push(data)
					console.log(doc.data())
					// this.users.full_name = doc.data().name;

				});
			})
		},
		methods: {
			previewImage(event) {
				this.uploadValue = 0;
				this.picture = null;
				this.imageData = event.target.files[0];

				const fi = document.getElementById('file');
				// Check if any file is selected.
				if (fi.files.length > 0) {

					console.log(fi.files.item(0).size)

					console.log(fi.files.length)
					for (let i = 0; i <= fi.files.length - 1; i++) {
						// Get the Bytes
						const fsize = fi.files.item(i).size;

						// Convert to MB
						const file = (fsize / (1024*1024)).toFixed(2);
						// The size of the file.
						if (file >= 4096) {
							alert(
								"File too Big, please select a file less than 4mb");
						} else {
							document.getElementById('file_size').innerHTML = 'File Size Approx: '+'<b>'
								+ file + '</b> MB';
						}
					}
				}

			},
			onUpload() {

				if (this.imageData && this.users.admission_no !== '') {

					db.collection('users').add({
						full_name: this.users.full_name,
						admission_no: this.users.admission_no,
						phone_no: this.users.phone_no,
						path_to_file: this.users.path_to_file,
						pdf_file_props: {
							size: this.users.pdf_file_props.size,
							no_of_copies: this.users.pdf_file_props.no_of_copies,
							double_sided: this.users.pdf_file_props.double_sided,
						},
						pending: this.users.pending,
						cost: this.users.cost

					}).then(docRef => {

						this.picture = null;
						const storageRef = firebase.storage()
							.ref(`${this.imageData.name}`).put(this.imageData);

						storageRef.on(`state_changed`, snapshot => {
								this.uploadValue = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
							}, error => {
								console.log(error.message)
							},
							() => {
								this.uploadValue = 100;
								storageRef.snapshot.ref.getDownloadURL().then((url) => {
									this.picture = url;
									console.log(this.picture);
									alert("File Uploaded!!");
								});
							}
						);

					}).catch(err => {
						console.log(err);
					});
				}
			}
		}
	}
</script>

