				     -webkit-box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1); position: absolute; top: 50%; left: 50%;
				     -moz-transform: translateX(-50%) translateY(-50%); -webkit-transform: translateX(-50%) translateY(-50%);
				     transform: translateX(-50%) translateY(-50%); ">

					<form class="col s12">
						<div class='row'>
							<div class='col s12'>
								<h5 class="indigo-text">NMIT XEROX</h5>
