								<!--								<label for='tel'>Phone Number</label>-->
								<div class="input-box">
									<input class="input_phone" v-model="phone_no" placeholder="Enter Phone Number"
									       id="tel" type="tel"
									       name="telphone" maxlength="10" required/>
									<span class="ph_prefix">+91 - </span>
								</div>
							</div>
						</div>

						<div class='row' id="otp_row">
							<div class='input-field col s12'>
								<input v-model="otp" id="otp" type="number" name="otp"/>
								<label for='otp'>Enter OTP</label>
							</div>
						</div>

						<div id="recaptcha" class="row">
							<div ref="recaptcha" class='col s12'></div>
						</div>

						<br/>
						<div style="text-align: center;">
							<div class='row' id="sendOtp_button">
								<button type='submit' name='btn_login' v-on:click="LoginPhone"
								        class='col s12 btn btn-medium waves-effect indigo'>Send OTP
								</button>
							</div>
						</div>

						<div style="text-align: center;">
							<div class='row' id="verifyOtp_button" style="display: none">
								<button type='submit' v-on:click="OtpVerification"
								        class='col s12 btn btn-medium waves-effect green'>
									Verify
								</button>
							</div>
						</div>


					</form>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import firebase from "firebase";

	let somethingGlobal = '';
	let coreResult;

	export default {
		name: 'login',
		data() {
			return {
				phone_no: '',
				otp: '',
			}
		},
		methods: {
			LoginPhone: function (e) {
				e.preventDefault();

				let prefix = '+91';
				let prefix_phone = prefix + this.phone_no

				console.log(prefix_phone)

				firebase.auth().signInWithPhoneNumber(prefix_phone, this.recaptcha)
					.then(function (confirmationResult) {
						// SMS sent. Prompt user to type the code from the message,
						// then sign the user in with
						// confirmationResult.confirm(code).
						window.confirmationResult = confirmationResult;
						coreResult = confirmationResult;
						alert("OTP Sented");

						somethingGlobal = "Inside LoginPhone";
						console.log(coreResult.toString());

						document.getElementById("verifyOtp_button").style.display = "block";
						document.getElementById("otp_row").style.display = "block";
						document.getElementById("sendOtp_button").style.display = "none";
						document.getElementById("recaptcha").style.display = "none";
						document.getElementById("phone_row").style.display = "none";

					}, err => {
						alert(err.message);
					}).catch(function (error) {
					// Error; SMS not sent
					console.log("Error 2: " + error);
				});
			},
			OtpVerification(e) {

				coreResult.confirm(this.otp).then(result => {
					alert("Successful Registered!");
					let user = result.user;
					console.log(user.uid);
					this.$router.go(this.$router.path);

				}).catch(error => {
					// alert(error);
					console.log(error);
					console.log(error);
				});

				e.preventDefault();
			}
		},
		mounted() {

			document.getElementById("otp_row").style.display = "none";

			somethingGlobal = "Mounting";
			console.log(somethingGlobal);
			this.recaptcha = new firebase.auth.RecaptchaVerifier(this.$refs['recaptcha']);
			this.recaptcha.render();

			// this.$recap = new firebase.auth.RecaptchaVerifier('recaptcha-container', {
			// 	'size': 'normal',
			// 	'callback': function(response) {
			// 		// reCAPTCHA solved, allow signInWithPhoneNumber.
			// 		// ...
			// 	},
			// 	'expired-callback': function() {
			// 		// Response expired. Ask user to solve reCAPTCHA again.
			// 		alert("Expired reCAPTCHA. Try again!")
			// 	}
			// });


		}
	}
</script>

